<template>
        <div class="modal d-block my-popups dialog">
            <loader :is-visible="isLoading"></loader>
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <slot name="header">
                            <h5 class="modal-title"> {{ $t('DeployItems') }}</h5>
                        </slot>
                        <button v-on:click="$emit('close')" type="button" title="" class="close" data-original-title="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                    </div>
                    <div class="modal-body">
                        <form>
                            <div class="d-flex flex-wrap">
                                <div class="col-md-12 p-0">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group">
                                                <label>{{ $t('Location') }}:<span class="text-danger">*</span></label>
                                                <div class="row">
                                                    <input type="hidden" id="hdnCompantLocationSelect">
                                                    <div class="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left" id="divmultilevelselect1" v-on:click="OnChangeLocation()">
                                                    </div>
                                                    <small v-if="validatelocation" class="float-left text-danger col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left font-weight-light">Please select the Location</small>
                                                      </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right">
                                            <a href="javascript:;" class="btn btn-success formbtn text-white mr-1" v-on:click="saveDeployItems()"><em class="fa fa-save pr-2"></em>{{ $t('Save') }}</a>
                                            <a v-on:click="$emit('close')" class="btn btn-danger  delete-btn formbtn widthhalf text-white"><em class="fa fa-close pr-2"></em>{{ $t('Cancel') }}</a>
                                        </div>
                                       
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
   
</template>
<script>
import DataService from "../../../services/DataService";

export default {    
    data() {
            return {
                isLoading:false,
                CompantLocationSelect: null,
                validatelocation: false,
            }
        },
        created(){
            this.isLoading=true;
            this.GetLocationByCompanyId();
        },
        methods: {
            GetLocationByCompanyId: function () {
            var vm=this;
            DataService.GetMultiLocationByCompanyId('')
          .then(function (response) {
              console.log(response);      
              debugger;   
              vm.isLoading=false;
            if (response.data.list != null) {
                              $("#divmultilevelselect1").show();
                              try {
                                  $("#divmultilevelselect1").RMultiLevelSelect({
                                      chartElement: '#divmultilevelselect1',
                                      chartClass: 'treeheight',
                                      data: response.data.list,
                                      id: "CompantLocationSelect",
                                      wrap: false,
                                      close: function () {
                                          var locId = $("#LocationId").val()
                                          var parentLocId = $("#hdnCompantLocationSelect").val();
                                          if ($("#hdnCompantLocationSelect").val() == "" || $("#hdnCompantLocationSelect").val() == "0") {
                                              $("span[data-valmsg-for='CompnayLocation']").addClass('invalid-feedback').html(String.Format(vm.$t("Required"),vm.$t("Location")));
                                             
                                          }
                                          else {
                                              $("span[data-valmsg-for='CompnayLocation']").removeClass('invalid-feedback').html('');
                                          }
                                          if (locId == parentLocId) {
                                              alert(vm.$t("ParentLocationNotSame"));
                                              $("#divmultilevelselect1").find("a.CompantLocationSelectdd-selected").removeAttr('style').html("Select");
                                          }
                                      }
                                  });
                                  $("#divmultilevelselect1").show();                                   
                              }
                              catch (ex) {   }
                          }
                          else {
                               
                              ShowMsgWithMsg(jsondata.msg, "failure");
                              $("#divmultilevelselect1").hide();
                          }
                           
          });     
         
        },
            saveDeployItems: function () {
                debugger;
                this.CompantLocationSelect = $('#hdnCompantLocationSelect').val();
                if (this.CompantLocationSelect != null && this.CompantLocationSelect != "") {
                    this.validatelocation = false;
                    $(".CompantLocationSelectdd-container").find("div.btn").removeClass("form-control is-invalid");
                    this.$parent.commonApprove(CompantLocationSelect.value);
                    this.$parent.showDeployModal = false;
                }
                else {
                    this.validatelocation = true;
                    $(".CompantLocationSelectdd-container").find("div.btn").addClass("form-control is-invalid");
                    $(".CompantLocationSelectselected").removeClass("w-80");
                }

            },
            OnChangeLocation: function () {
                debugger; 
                var vm=this;
                vm.isLoading=true;
                setTimeout(function(){
              $("#divmultilevelselect1").find('ul#CompantLocationSelect').css('display','block');
              vm.CompantLocationSelect = $('#hdnCompantLocationSelect').val();
                if (vm.CompantLocationSelect != null && vm.CompantLocationSelect != "") {
                    $("#divmultilevelselect1").find('ul#CompantLocationSelect').css('display','none');
                    vm.validatelocation = false;
                    $(".CompantLocationSelectdd-container").find("div.btn").removeClass("form-control is-invalid");
                }
               vm.isLoading=false;},300);                
            },
        }
    }
  
</script>